import React from "react";

const Item = ({ payload, onChildClick, children }) => {
  return (
    <div className="left-section" onClick={() => onChildClick(payload.content)}>
      {children}
    </div>
  );
};

const Detail = (props) => {
  return <div className="right-section">{props.children}</div>;
};

const MasterDetail = (props) => {
  return (
    <div>
      {/* {console.log(props.children[2].props.children)} */}
      <h2>Item Details</h2>
      <div className="master-detail-container"> {props.children} </div>
    </div>
  );
};

MasterDetail.Detail = Detail;
MasterDetail.Item = Item;

export default MasterDetail;
