import React, { useState } from "react";
import "./styles.css";
import MasterDetail from "./components/MasterDetail";

export default function App() {
  // Implement a Master Detail component that renders a list
  // of items on the left and a detail view on the right.
  // When the user clicks on a list item the render function
  // of the detail view should be called with the corresponding payload.
  // The component should provide the following API:
  const [payload, setPayload] = useState("Hello Peers");
  const childClickHandle = (content) => {
    setPayload(content);
  };
  return (
    <MasterDetail>
      <div>
        <MasterDetail.Item
          onChildClick={childClickHandle}
          payload={{ content: "Hello Peers" }}
        >
          Intro
        </MasterDetail.Item>
        <MasterDetail.Item
          onChildClick={childClickHandle}
          payload={{ content: "Welcome to Cool Company" }}
        >
          Welcome
        </MasterDetail.Item>
      </div>
      <MasterDetail.Detail>{payload}</MasterDetail.Detail>
    </MasterDetail>
  );
}
